.title-table-wrapper {
    display: flex;
    gap: 69px;
}

.elevate-title {
    padding-top: 65px;
    text-align: center;
}

.elevate-table-wrapper {
    display: flex;
    justify-content: space-between;
}

.image-text-wrapper {
    display: flex;
    gap: 50px;
}

.elevate-table-element {
    gap: 51px;
    padding-top: 50px;
    border-bottom: 1px solid #D9D9D9;
    user-select: none;
}

.last-elevate-in-row {
    border-bottom: 0;
}

.elevate-table-element:hover {
    background-color: #D4E2F5;
    transition: .4s;
}

.elevate-table {
    width: 100%;
    gap: 30px;
    justify-content: space-between;
    padding-bottom: 88px;
}

.elevate-table-right-part {
    display: flex;
}

.elevate-table-left-part {
    display: flex;
}

.table-number {
    font-weight: 600;
    font-size: 16px;
    line-height: 22.4px;
    color: #003399;
    padding-left: 9px;
    padding-top: 12px;
    padding-bottom: 12px;
}

.table-text-subtitle {
    color: #221F32;
    font-weight: 400;
    font-size: 15px;
    line-height: 21px;
    width: 60%;
    opacity: .7;
    padding-bottom: 29px;
}

.table-image img {
    width: 48px;
    height: 46px;
}



.table-text-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 25.2px;
}


@media (max-width: 1230px) {
	.elevate-table-element {
		gap: 23px;
	}

	.image-text-wrapper {
		gap: 20px;
	}


	.table-text-subtitle {
		width: 65%;
		padding-bottom: 8px;
	}

	.table-text-title {
		font-size: 17px;
		line-height: 23.8px;
	}
}

@media (max-width: 400px) {
	.elevate-table.row {
        flex-wrap: wrap;
    }

    .title-table-wrapper {
        gap: 27px;
    }

    .elevate-table-element {
        padding-top: 0;
        flex-direction: row-reverse;
    }

    .elevate-table-right-part {
        display: flex;
        gap: 10px;
    }

    .elevate-table-left-part {
        padding-top: 10px;
        gap: 10px;
    }
    

    .elevate-table {

        gap: 0px;

    }

    .table-text-subtitle {
        width: 100%;
    }

    .table-image img {
        width: 33px;
        height: 33px;
    }
}